r: pointer;\n    padding: 12px 40px;\n    color: white;\n    border: none;\n    border-radius: 5px;\n    background-color: #1B1717;\n  }\n  \n  .headline__button a {\n    padding: 20px;\n    color: white;\n    text-decoration: none;\n  }\n  .headline__button a:visited {\n    color: white;\n  }\n\n  .headline__button:focus {\n    color: #FFF;\n    background-color: crimson;\n  }\n\n  .headline__button:hover {\n    color: #FFF;\n    background-color: crimson;\n  }\n  \n  main {\n    width: 100%;\n    margin: 0 auto;\n}\n\n/* card */\n\n.card {\n    width: 100%;\n    margin: 55px auto;\n    text-align: center;\n}\n\n.card-label {\n    text-align: center;\n    font-size: 32px;\n    font-weight: bolder;\n}\n\n.card-label::after {\n    content: '';\n    margin-top: 15px;\n    display: block;\n    border-bottom: 1px solid #d4d4d4;\n}\n\n/* restaurant */\n\n.restaurant {\n    margin: 32px auto auto;\n    text-align: left;\n    padding-bottom: 40px;\n}\n\n.restaurant-list {\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);\n    width: 100%;\n    border-radius: 5px;\n    overflow: hidden;\n}\n\n.restaurant-list-content {\n    padding: 20px;\n    margin-top: -30px;\n}\n\n.restaurant-list-location {\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    background-color: crimson;\n    color: #fff;\n    font-weight: 550;\n    width: 50%;\n    padding: 7px;\n    border-radius: 0 10px 10px 0;\n    text-align: center;\n}\n\n.restaurant-list-thumbnail {\n    width: 100%;\n}\n\n.restaurant-list-rating {\n    font-size: 13px;\n    text-transform: uppercase;\n    color: lightseagreen;\n    font-weight: bold;\n}\n\n.restaurant-list-rating-star {\n    font-size: 14px;\n    color: orange;\n    text-decoration: none;\n    font-weight: bold;\n    margin-top: 10px;\n}\n\n.restaurant-list-title {\n    font-weight: bold;\n    margin-top: 16px;\n}\n\n.restaurant-list-title a {\n    text-decoration: none;\n    color: inherit;\n}\n\n.restaurant-list-description {\n    margin-top: 12px;\n    font-size: 11px;\n    line-height: 20px;\n    text-align: justify;\n    overflow: hidden;\n    display: -webkit-box;\n    -webkit-line-clamp:4;\n    -webkit-box-orient: vertical;\n}\n#tombol a:visited{\n  color: white;\n}\n#tombol a {\n  color: white;\n  text-decoration: none;\n}\n\n#tombol {\n  font-family: 'kanit';\n  text-decoration: none;\n    border: none;\n    display: block;\n    margin-top: 20px;\n    color: #fff;\n    border-radius: 5px;\n    background-color: #1B1717;\n    text-align: center;\n    width: 100%;\n    min-height: 45px;\n    font-size: 16px;\n    text-transform: uppercase;\n}\n#tombol:focus {\n  background-color: crimson;\n}\n\n#tombol:hover {\n    background-color: crimson;\n  }\n\n\n  .daftar-restoran h2 {\n    margin-top: 40px;\n    font-size: 30px;\n    text-align: center;\n    padding-bottom: 5px;\n    margin-bottom: 15px;\n    border-bottom: 3px solid crimson;\n    display: inline-block;\n}\n\n.daftar-restoran p {\n    font-size: 18px;\n    font-weight: 600;\n}\n\n.restoran { \n    margin: 50px 20px;\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n    grid-gap: 30px;\n    align-items: center;\n}\n\n.info_restoran{\n    margin: 50px 20px;\n}\n\n.info_restoran > img{\n    border-radius: 10px;\n    width: 100%;\n    margin: 0 auto;\n}\n\n.background_tabel_info_restoran{\n    margin: 10px auto;\n    background-color: white;\n    padding: 30px;\n    border-radius: 10px;\n    width: 100%;\n    overflow: auto;\n    -ms-overflow-style: none;\n    text-align: left;\n}\n\n.background_tabel_info_restoran::-webkit-scrollbar { \n    display: none;\n}\n\n.tabel_info_restoran{\n    width: 100%;\n}\n\n.normal_p{\n    font-size: 14px !important;\n    font-weight: normal !important;\n}\n\n.review_restoran{\n    background-color: white;\n    padding: 20px;\n    border-radius: 10px;\n    grid-column-start: 1;\n    grid-column-end: 3;\n    width: 100%;\n    margin: 10px auto;\n}\n\n.review_restoran > h3 {\n    font-size: 16px;\n}\n \n\n.review_restoran > h3 {\n    font-size: 16px;\n}\n\n.contentRes {\n  background-color: #f4f4f4;\n  padding: 50px 0;\n  text-align: center;\n}\n\n.favorite {\n  font-size: 18px;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  background-color: crimson;\n  color: white;\n  border: 0;\n  border-radius: 50%;\n  width: 55px;\n  height: 55px;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n/* footer */\n\n\nfooter {\n    background-color: crimson;\n    color: #fff;\n    padding: 3em;\n    width: 100%;\n    text-align: center;\n    align-items: center;\n    justify-content: center;\n  }\n  footer p {\n    font-size: 13px;\n  }\n  \n  .container {\n    max-width: 1140px;\n    width: 100%;\n    padding-right: 15px;\n    padding-left: 15px;\n    margin-right: auto;\n    margin-left: auto;\n}\n\n  footer .social-media {\n    position: relative;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    margin: 11px 0;\n    flex-wrap: wrap;\n  }\n  \n  footer .social-media li {\n    list-style: none;\n  }\n  footer .social-media li a {\n   font-size: 44px;\n    color: #FFF;\n    margin: 0 10px;\n  }\n  \n  \n  "],"sourceRoot":""}]);
